export default {
	data() {
		return {
			infoList: [{
				name: '诸葛亮',
				headImg: '../../static/images/shuijiao.jpg',
				con: 'Hi，最近有一个新的活动邀您共同参加，活动报名方式已发您手机短信，请注意查收。',
				time: '2020-03-24 10:00:00',
			}]
		}
	},
	methods: {


	},
	mounted() {
		var that = this;
		var data = this.infoList;
		data.map((item) => {
			item.time = that.$commonJs.timeFormat.noYearFormat(item.time)
			return item
		})
	}
